.list-container {
    position: relative;
    margin: auto;
    height: 85vh;
    width: 50vw;
    border-radius: 22px;
    box-shadow: 0px 50px 50px rgba(0, 0, 0, 0.15);
    background: linear-gradient(209.22deg, #ffffff 4.25%, #f1f1f1 95.6%);
    padding: 1rem;
}

.list-container h1 {
    font-weight: 300;
    text-transform: uppercase;
}

.list-container form {
    position: relative;
    display: flex;
    justify-content: center;
    max-width: 280px;
    margin: 0 auto;
}

.list-container input[type="text"] {
    border: none;
    border-bottom: solid 1px rgba(0,0,0,0.23);
    outline: none;
    font-size: 1.5rem;
    background: none;
    text-align: center;
    width: auto;
}

.list-container input[type="submit"] {
    background: none;
    border: none;
    box-shadow: 2px 3px 5px 2px rgba(0, 0, 0, 0.13);
    padding: 5px;
    outline: none;
    position: absolute;
    right: 0;
}

.list-container input[type="submit"]:hover {
    cursor: pointer;
}

.list ul {
    list-style: none;
    margin: 2rem 0 0 0;
    padding: 0;
}

.list {
    max-height: 75%;
    margin-top: 1rem;
    overflow-y: scroll;
}

.list li {
    position: relative;
    padding: 0.5rem 0;
    max-width: 60%;
    margin: 0 auto;
    border-bottom: 1px solid rgba(0,0,0,0.13);
}

.list li button {
    position: absolute;
    right: 0;
    width: 1rem;
    height: 1rem;
    background-color: #F00;
    border: none;
    outline: none;
}


@media screen and (max-width:780px) {
    .list-container {
        width: 80vw;
    }

    .list-container h1 {
        font-size: 1.5rem;
    }

    .list li {
        max-width: 100%;
        font-size: 0.8rem;
    }


}
